olute;\n  top: 50%;\n  transform: translateY(-50%);\n  color: #fff;\n  border: none;\n  padding: 10px 20px;\n  font-size: 36px;\n  cursor: pointer;\n  z-index: 10;\n  border-radius: 50%;\n  transition: background 0.3s;\n  \n  &:before {\n    content: none;\n  }\n}\n\n.slick-prev.custom-arrow {\n  left: 60px; // позиція лівої стрілки\n}\n\n.slick-next.custom-arrow {\n  right: 100px; // позиція правої стрілки\n}\n\n.decorative-line {\n  position: absolute;\n  bottom: 25px;\n  margin-inline: 60px;\n  left: 0;\n  right: 0;\n  background-color: $black;\n  height: 1px;\n  \n  &:before, &:after {\n    content: \"\";\n    position: absolute;\n  }\n  \n  &:before {\n    background: url('../images/decor-1.svg') no-repeat;\n    width: 41px;\n    top: -4px;\n    height: 9px;\n  }\n  \n  &:after {\n    background: url('../images/clover.svg') no-repeat;\n    width: 22px;\n    height: 22px;\n    top: -11px;\n    right: -20px;\n  }\n  \n  &--footer {\n    top: 0;\n    margin-inline: 0;\n    background-color: rgba(10, 15, 19, 0.2);\n    margin-left: 60px;\n    margin-right: 100px;\n    \n    &:before {\n      left: -60px;\n    }\n    \n    &:after {\n      color: #0A0F13;\n      font-size: 14px;\n      content: 'Smarto';\n      text-transform: uppercase;\n      font-weight: 500;\n      background-image: none;\n      right: -60px;\n      line-height: 1;\n      top: -6px;\n    }\n  }\n}\n\n.portfolio {\n  padding-block: 100px;\n  \n  &__title {\n    font-size: 48px;\n    font-weight: 500;\n    margin-bottom: 40px;\n  }\n  \n  &__tabs {\n    display: flex;\n    gap: 60px;\n    font-size: 24px;\n    font-weight: 600;\n    margin-bottom: 100px;\n    cursor: pointer;\n    overflow: scroll;\n  }\n  \n  &__tabs-item--active {\n    text-decoration: underline;\n  }\n  \n  &__loading,\n  &__empty,\n  &__error {\n    font-size: 18px;\n    color: $greyDark;\n    grid-column: 1/-1;\n  }\n  \n  &__load-more {\n    margin-top: 132px;\n    align-items: center;\n    gap: 10px;\n    font-size: 14px;\n    text-transform: uppercase;\n    border-bottom: 1px solid $black;\n    background: transparent;\n    padding: 0;\n    cursor: pointer;\n    font-weight: 600;\n    transition: opacity .2s ease;\n    display: block;\n    margin-inline: auto;\n    \n    &:disabled {\n      opacity: .6;\n      cursor: default;\n    }\n  }\n  \n  &__spinner {\n    width: 16px;\n    height: 16px;\n    border: 2px solid $greyLight;\n    border-top-color: $greyDark;\n    border-radius: 50%;\n    display: none;\n    animation: spin 0.7s linear infinite;\n  }\n  \n  .is-loading &__spinner {\n    display: inline-block;\n  }\n}\n\n@keyframes spin {\n  from {\n    transform: rotate(0);\n  }\n  to {\n    transform: rotate(360deg);\n  }\n}\n\n// Social icons\n.contacts-social {\n  &__list {\n    display: flex;\n    gap: 16px;\n    align-items: center;\n  }\n  \n  &__list-item-link {\n    display: inline-flex;\n    align-items: center;\n    justify-content: center;\n  }\n}\n\n.contacts-social__list-item {\n  .contacts-social__icon {\n    height: 40px;\n    width: 40px;\n  }\n}\n\n.contacts-social__icon {\n  width: 24px;\n  height: 24px;\n  display: block;\n}\n\n.footer__title {\n  font-size: 64px;\n  font-weight: 600;\n  margin-bottom: 30px;\n}\n\n.contacts {\n  display: grid;\n  grid-template-columns: 1fr 1fr;\n  gap: 84px;\n}\n\n.contacts-office__title,\n.contacts-social__title,\n.contacts-phones__title,\n.contacts-email__title {\n  font-size: 24px;\n  margin-bottom: 23px;\n}\n\n.contacts-office__mail {\n  display: block;\n  font-weight: 500;\n  margin-bottom: 42px;\n}\n\n.contacts-office__button {\n  display: inline-flex;\n  align-items: center;\n  justify-content: center;\n  gap: 8px;\n  font-size: 14px;\n  padding: 13px 20px;\n  color: $white;\n  border: 1px solid $black;\n  border-radius: 999px;\n  font-weight: 600;\n  background: $black;\n}\n\n.contacts-office__button:hover {\n  background: $black;\n  color: $white;\n  border-color: $black;\n}\n\n.contacts-office__button-icon {\n  width: 18px;\n  height: 18px;\n  display: inline-block;\n}\n\n.contacts-phones__list {\n  display: grid;\n  gap: 8px;\n}\n\n.contacts-phones__item {\n  display: flex;\n  align-items: baseline;\n  gap: 10px;\n}\n\n.contacts-phones__link {\n  font-weight: 600;\n}\n\n.contacts-phones__note {\n  color: $greyDark;\n  font-size: 12px;\n}\n\n.contacts-email__link {\n  font-weight: 500;\n  font-size: 20px;\n}\n\n.footer__right {\n  isolation: isolate;\n  flex: 1;\n  \n  &-images {\n    position: relative;\n  }\n  \n  &-images-image {\n    max-width: 680px;\n    height: auto;\n    position: absolute;\n    right: 0;\n    \n    &:first-of-type {\n      z-index: 2;\n      right: 150px;\n    }\n  }\n  \n  &-label {\n    position: absolute;\n    left: 77px;\n    top: 80px;\n    font-size: 96px;\n    color: #503b27;\n    z-index: 3;\n    line-height: 1;\n    mix-blend-mode: hard-light;\n  }\n  \n  &-tag {\n    position: absolute;\n    right: 142px;\n    bottom: -136px;\n    font-size: 96px;\n    color: white;\n    z-index: 3;\n    line-height: 1;\n    mix-blend-mode: exclusion;\n  }\n}\n\n.footer {\n  padding-block: 200px;\n  &__inner {\n    display: flex;\n    justify-content: space-between;\n  }\n  \n  &__left {\n    padding-top: 50px;\n    position: relative;\n    max-width: 880px;\n    width: 100%;\n  }\n  \n  &__right {\n    position: relative;\n    display: flex;\n    justify-content: flex-end;\n  }\n}\n\n.portfolio {\n  &__list {\n    display: grid;\n    grid-template-columns: repeat(3, 1fr);\n    grid-gap: 160px;\n  }\n  \n  &__card {\n    max-width: 440px;\n    width: 100%;\n    border: 1px solid $greyLight;\n    border-bottom: none;\n    position: relative;\n    \n    &-inner {\n      padding: 20px 12px;\n    }\n    \n    &:before, &:after {\n      content: \"\";\n      position: absolute;\n    }\n    \n    &:before {\n      background: url('../images/pin.svg') no-repeat;\n      width: 50px;\n      height: 52px;\n      top: 0;\n      left: 50%;\n      transform: translate(-50%, -50%);\n    }\n    \n    &:after {\n      background: url('../images/card-bottom.svg') no-repeat;\n      width: 100%;\n      height: 41px;\n      bottom: 0;\n      transform: translateY(100%);\n    }\n  }\n  \n  &__card-top {\n    display: flex;\n    justify-content: space-between;\n    margin-bottom: 28px;\n  }\n  \n  &__card-top-year {\n    font-size: 20px;\n    font-weight: 500;\n  }\n  \n  &__card-top-type {\n    line-height: 140%;\n    text-transform: uppercase;\n    font-weight: 500;\n    padding: 4px 8px;\n    border: 1px solid $grey;\n    border-radius: 20px;\n  }\n  \n  &__card-image {\n    margin-bottom: 12px;\n  }\n  \n  &__card-title {\n    margin-bottom: 28px;\n    font-size: 24px;\n  }\n  \n  &__card-address {\n    font-size: 20px;\n    font-weight: 500;\n    margin-bottom: 28px;\n  }\n  \n  &__card-tags-title {\n    font-size: 14px;\n    font-weight: 500;\n    margin-bottom: 9px;\n    text-transform: uppercase;\n  }\n  \n  &__card-tags-list {\n    display: flex;\n    flex-wrap: wrap;\n    gap: 2px;\n  }\n  \n  &__card-tags-list-item {\n    font-size: 14px;\n    border: 1px solid $greyLight;\n    border-radius: 20px;\n    padding: 4px 8px;\n    color: $greyDark;\n    font-weight: 500;\n    text-transform: uppercase;\n  }\n}\n\n.contacts {\n  &-office__title {\n    font-size: 24px;\n    font-weight: 600;\n  }\n}\n\n@media (max-width: 1800px) {\n  .footer {\n    &__inner {\n      justify-content: center;\n    }\n    &__left {\n      max-width: 100%;\n    }\n    &__right {\n      order: -1;\n      display: none;\n    }\n  }\n}\n\n@media (max-width: 1200px) {\n  .portfolio {\n    &__list {\n      grid-template-columns: repeat(2, 1fr);\n    }\n  }\n}\n\n@media (max-width: 900px) {\n  .footer {\n    &__right-images-image {\n      max-width: 380px;\n    }\n  }\n}\n\n@media (max-width: 768px) {\n  .header {\n    &__slider-slide-title {\n      font-size: 36px;\n      padding-block: 150px;\n    }\n  }\n  \n  .slick-prev, .slick-next {\n    height: auto;\n  }\n  \n  .slick-prev.custom-arrow {\n    left: 30px; // позиція лівої стрілки\n  }\n  \n  .slick-next.custom-arrow {\n    right: 60px; // позиція правої стрілки\n  }\n  \n  .decorative-line {\n    margin-inline: 30px;\n    \n    &:before {\n      left: -15px;\n    }\n  }\n  .portfolio {\n    padding-block: 50px;\n    \n    &__title {\n      font-size: 30px;\n    }\n    \n    &__list {\n      grid-template-columns: repeat(1, 1fr);\n      gap: 80px;\n    }\n    \n    &__tabs {\n      margin-bottom: 50px;\n    }\n    \n    &__card {\n      margin-inline: auto;\n    }\n    \n    &__card-title {\n      font-size: 20px;\n    }\n    \n    &__card-address {\n      font-size: 16px;\n      margin-bottom: 20px;\n    }\n    \n    &__tabs-item {\n      font-size: 16px;\n    }\n    \n    &__load-more {\n      margin-bottom: 60px;\n    }\n  }\n  \n  .contacts {\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    gap: 30px;\n  }\n  \n  .footer {\n    padding-block: 50px;\n    &__title {\n      font-size: 44px;\n    }\n  }\n  \n  .contacts-office__title, .contacts-social__title, .contacts-phones__title, .contacts-email__title {\n    font-size: 24px;\n  }\n  \n  .decorative-line {\n    &--footer {\n      margin-right: 120px;\n      margin-left: 15px;\n    }\n  }\n}\n\n@media (max-width: 400px) {\n  .slick-prev.custom-arrow {\n    left: 0; // позиція лівої стрілки\n  }\n  \n  .slick-next.custom-arrow {\n    right: 40; // позиція правої стрілки\n  }\n}"],"sourceRoot":""}]);
